import '../../../libs/slick/slick.min';
import '../../../libs/popup/jquery.magnific-popup.min';
import countries from "../../../data/countries";

$(document).ready(function () {

  //header
  $(window).scroll(function () {
    const windowWidth = $(this).width();
    const windowHeight = $(this).height();
    const windowScrollTop = $(this).scrollTop();

    if(windowScrollTop > 200) {
      $(".header").addClass("small-blue");
    } else {
      $(".header").removeClass("small-blue");
    }
  });

  //sliders

  $('.slider-best').slick({
    dots: false,
    arrows: false,
    infinite: true,
    speed: 600,
    autoplay: false,
    autoplaySpeed: 2000,
    slidesToShow: 3,
    slidesToScroll: 1,
    responsive: [
      {
        breakpoint: 1200,
        settings: {
          centerMode: true,
          centerPadding: '26%',
          slidesToShow: 1,
          slidesToScroll: 1,
        }
      },
      {
        breakpoint: 992,
        settings: {
          centerMode: true,
          centerPadding: '20%',
          slidesToShow: 1,
          slidesToScroll: 1,
        }
      },
      {
        breakpoint: 540,
        settings: {
          centerMode: true,
          centerPadding: '5px',
          slidesToShow: 1,
          slidesToScroll: 1,
        }
      }
    ]
  });

  $('.slider-countries').slick({
    slidesToShow: 3,
    slidesToScroll: 1,
    dots: false,
    arrows: false,
    focusOnSelect: true,
    speed: 600,
    autoplay: false,
    autoplaySpeed: 2000,
    responsive: [
      {
        breakpoint: 1200,
        settings: {
          centerMode: true,
          dots: true,
          centerPadding: '18%',
          slidesToShow: 2,
          slidesToScroll: 1,
        }
      },
      {
        breakpoint: 992,
        settings: {
          centerMode: true,
          dots: true,
          centerPadding: '5%',
          slidesToShow: 2,
          slidesToScroll: 1,
        }
      },
      {
        breakpoint: 540,
        settings: {
          centerMode: false,
          dots: true,
          centerPadding: '0',
          slidesToShow: 3,
          slidesToScroll: 1,
          vertical: true
        }
      }
    ]
  });

  $('.team-slider').slick({
    dots: true,
    arrows: false,
    infinite: true,
    speed: 600,
    autoplay: false,
    autoplaySpeed: 2000,
    slidesToShow: 3,
    slidesToScroll: 1,
    responsive: [
      {
        breakpoint: 1200,
        settings: {
          centerMode: true,
          centerPadding: '26%',
          slidesToShow: 1,
          slidesToScroll: 1,
        }
      },
      {
        breakpoint: 992,
        settings: {
          centerMode: true,
          centerPadding: '20%',
          slidesToShow: 1,
          slidesToScroll: 1,
        }
      },
      {
        breakpoint: 540,
        settings: {
          centerMode: false,
          centerPadding: '0',
          slidesToShow: 1,
          slidesToScroll: 1,
        }
      }
    ]
  });

  $('.boat-slider').slick({
    dots: false,
    arrows: false,
    infinite: true,
    speed: 600,
    autoplay: false,
    autoplaySpeed: 2000,
    slidesToShow: 5,
    slidesToScroll: 1,
    responsive: [
      {
        breakpoint: 1200,
        settings: {
          centerMode: true,
          centerPadding: '12%',
          slidesToShow: 2,
          slidesToScroll: 1,
        }
      },
      {
        breakpoint: 992,
        settings: {
          centerMode: true,
          centerPadding: '20%',
          slidesToShow: 2,
          slidesToScroll: 1,
        }
      },
      {
        breakpoint: 540,
        settings: {
          centerMode: true,
          centerPadding: '70px',
          slidesToShow: 1,
          slidesToScroll: 1
        }
      }
    ]
  });

  $('.circle_prev').on('click', function () {
    $('.slider-countries').slick('slickPrev')
  });

  $('.circle_next').on('click', function () {
    $('.slider-countries').slick('slickNext')
  });

  //popup

  $('.popup-youtube').magnificPopup({
    disableOn: 700,
    type: 'iframe',
    fixedContentPos: false,
    fixedBgPos: true,
    overflowY: 'auto',
    closeBtnInside: true,
    preloader: false,
    midClick: true,
    removalDelay: 300,
    mainClass: 'my-mfp-zoom-in'
  });

  // Search

  const input = document.querySelector("#search-field");
  const list = document.querySelector("#search-list");

  input.addEventListener("keyup", complete);
  list.addEventListener("click", selectWord);

  function complete() {
    const val = input.value.trim().toLocaleLowerCase();
    list.style.display = "block";
    if(val.length > 1) {
      const country = countries.filter(county => county.name.toLocaleLowerCase().indexOf(val) === 0);

      if(country.length >= 1) {
        list.innerHTML = renderHtml(country);
      } else {
        list.innerHTML = `<li class="form-select__item"><span>No results...</span></li>`;
      }
    } else {
      list.innerHTML = `<li class="form-select__item"><span>The list is too short...</span></li>`;
    }
  }

  function selectWord(e) {
    e.preventDefault();
    const target = e.target;
    if(target.parentNode.nodeName === "LI") {
      input.value = target.innerHTML;
      list.style.display = "none";
    }
  }

  function renderHtml(arr) {
    let html = "";
    for (let i = 0; i < arr.length; i++) {
      html += `<li class="form-select__item"><a href="#">${ arr[i].name }</a></li>`
    }

    return html;
  }

  $(document).on('click', function (e) {
    if(!$(e.target).closest(".search").length) {
      $("#search-list").css("display", "none");
    }
    e.stopPropagation();
  });
});